import { useState } from "react";

export default function Calendar() {
  const todaysDate = new Date;
  const thisDay = todaysDate.getMonth();
  const thisMonth = todaysDate.getDate();
  const thisYear = todaysDate.getFullYear();
  const [month, setMonth] = useState(thisMonth);
  const [year, setYear] = useState(thisYear);

  function previous() {
    setMonth(todaysDate.setMonth(todaysDate.getMonth() - 1));
    console.log(month);
  }

  return (
    <div>
      <div>
        <h1>Calendar</h1>
      </div>
      <div>
        <button onClick={previous}>Previous</button>
        <button>Next</button>
      </div>
      <div>
        <h2>{`${thisMonth.toString()}, ${thisYear.toString()}`}</h2>
        <table>
          <thead>
            <tr>
              <th>Mon</th>
              <th>Tues</th>
              <th>Weds</th>
              <th>Thurs</th>
              <th>Fri</th>
              <th>Sat</th>
              <th>Sun</th>
            </tr>
          </thead>
        </table>
      </div>
    </div>
  )
}